import {
    Streamlit,
    StreamlitComponentBase,
    withStreamlitConnection,
} from "streamlit-component-lib"
import React from "react"


class StopButton extends StreamlitComponentBase {
    render() {
        return (
            <div>
                <button
                    onClick={
                        () => {
                            Streamlit.setComponentValue([true])
                        }
                    }
                    style={{
                        border: "none",
                        backgroundColor: "#000",
                        cursor: "pointer",
                        height: 60,
                        width: 90,
                        outline: "none",
                        borderRadius: "50px",
                    }}
                >
                    <svg xmlns="http://www.w3.org/2000/svg"
                         width="24" height="24"
                         viewBox="0 0 24 24"
                         fill={'#fff'}
                    >
                        <path d="M2 2h20v20h-20z"/>
                    </svg>
                </button>
            </div>
        );
    }
}

export default withStreamlitConnection(StopButton)
